import { gsap } from 'gsap';
import { ScrollSmoother } from 'gsap/ScrollSmoother';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

//* _____________  - ScrollTrigger, ScrollSmoother __________________
gsap.registerPlugin(ScrollTrigger, ScrollSmoother);

//* _____________________  - ScrollTrigger _________________________
ScrollTrigger.normalizeScroll(false);
ScrollTrigger.config({ ignoreMobileResize: true });

//* ----------------------------------------------------------------------------
export const smoother = ScrollSmoother.create({
	wrapper: '#wrapper',
	content: '#content',
	speed: 1,
	smooth: 1.5,
	effects: true,
	smoothTouch: 0.1,
});

//* ___________________________ "applyParallax" ________________________________
export function applyParallax(element) {
	const smoother = ScrollSmoother.get();
	smoother.effects(element, {
		speed: () => 0.5
	});
}
