//* __________________   "performance" ______________________
export function tlVerticalOpacity() {
	gsap.to('.el-opacity, .bg-section__image', {
		opacity: 0, //  
		ease: 'none', //    
		scrollTrigger: {
			trigger: '.performance',
			start: 'top top', //  ,  `.performance`  80%  
			//   
			end: 'bottom top', //  ,  `.performance`  
			// 
			scrub: 1, //       
			// markers: true, //   (  )
		}
	});
}

//* _______________________   "Waves" __________________________
export function tlVerticalReverse() {
	gsap.to('.vertical-reverse', {
		opacity: 0.5, //  
		ease: 'none', //    
		scrollTrigger: {
			trigger: '.performance',
			/*  ,  `.performance`  80%   
			  */
			start: 'top top',
			/*  ,  `.performance`  
			 */
			end: 'bottom top',
			scrub: 2, //       
			// markers: true, //   (  )
		}
	});
}

